<template>
  <v-col cols="12">
    <v-row>
      <!-- ОПЕРАТОР -->
      <v-col
        :cols="12"
        :sm="3"
        class="py-0"
        style="text-align: left;"
        :style="$vuetify.breakpoint.xs ? 'padding-left: 0 !important;' : ''"
      >
        <v-btn-toggle
          v-model="localRule.execution.bid_operator"
          color="primary"
          borderless
          mandatory
          class="py-2"
        >
          <v-btn 
            value="+"
            style="font-size: 12px; height: 24px;"
          >
            <span>+</span>
          </v-btn>

          <v-btn 
            value="-"
            style="font-size: 12px; height: 24px;"
          >
            <span>-</span>
          </v-btn>
        </v-btn-toggle>
      </v-col>

      <!-- ПРОЦЕНТЫ БЮДЖЕТА -->
      <v-col
        :cols="12"
        :sm="3"
        class="py-0 pl-0"
      >
        <v-text-field
          :value="Math.abs(localRule.execution.bid_percents)"
          maxlength="4"
          outlined
          dense
          hide-details
          append-icon="fas fa-percent"
          :error="!localRule.execution.bid_percents && showWarnings"
          @input="bidPercentsChanged($event)"
        />
      </v-col>
      <v-col
        :cols="12"
        :sm="6"
        class="py-0"
        :style="$vuetify.breakpoint.xs ? 'margin-top: 16px; padding-left: 0 !important;' : ''"
      >
        <!-- МАКСИМАЛЬНЫЙ БЮДЖЕТ -->
        <v-text-field
          v-show="localRule.execution.bid_operator === '+'"
          :value="localRule.execution.bid_max"
          :label="$t('autoRules.fbRules.static.bidMax')"
          outlined
          dense
          hide-details
          @input="bidMax($event)"
        />

        <!-- МИНИМАЛЬНЫЙ БЮДЖЕТ -->
        <v-text-field
          v-show="localRule.execution.bid_operator === '-'"
          :value="localRule.execution.bid_min"
          :label="$t('autoRules.fbRules.static.bidMin')"
          outlined
          dense
          hide-details
          @input="bidMin($event)"
        />
      </v-col>
    </v-row>
  </v-col>
</template>

<script>
import { mapFields }        from 'vuex-map-fields';
import {mapGetters}         from 'vuex';

export default {
  name: 'RulesMainDialogBid',

  props: {
    localRule: {
      type: Object,
      default: () => ({})
    }
  },

  data() {
    return {
      bidOperator: ['+', '-']
    };
  },

  computed: {
    ...mapGetters({
      showWarnings: 'rules/showWarnings'
    }),
    ...mapFields('rules', ['rules']),
  },

  created () {
    if (this.localRule.execution.bid_change_amount) {
      this.$store.dispatch('rules/setForMainDialogExecutionOptions', {option: 'bid_percents', value: this.localRule.execution.bid_change_amount});
    }
  },

  methods: {
    bidPercentsChanged(event) {
      let bidPercents = parseFloat(event);

      if (isFinite(bidPercents) && bidPercents > 1000) {
        bidPercents = 1000;
      } else if (!isFinite(bidPercents)) {
        bidPercents = null;
      }

      this.rules.forMainDialog.execution.bid_percents = bidPercents;
    },

    bidMax(event) {
      this.localRule.execution.bid_max = event.toString().trim().replace(',', '.');
    },
    bidMin(event) {
      this.localRule.execution.bid_min = event.toString().trim().replace(',', '.');
    }
  }
};
</script>