<template>
  <v-row class="topbar-row">
    <!-- ЛЕВАЯ ЧАСТЬ -->
    <v-col
      cols="12"
      sm="9"
    >
      <!-- ДЕЙСТВИЯ -->
      <div>
        <topbar-actions />
      </div>

      <div
        style="float: right; width: 170px; text-align: center;"
        class="pt-0 pb-2 mr-8"
      >
        <!-- КНОПКА СОЗДАТЬ ГРУППУ ПРАВИЛ -->
        <v-btn
          small
          text
          color="primary"
          style="margin-top: 2px;"
          class="white--text"
          @click="createRulesGroup"
        >
          <v-icon
            :size="18"
            class="mr-1"
          >
            add
          </v-icon>
          {{ $t('autoRules.createRulesGroup') }}
        </v-btn>
      </div>
    </v-col>

    <!-- ПРАВАЯ ЧАСТЬ -->
    <v-col
      cols="12"
      sm="3"
    >
      <v-row>
        <!-- ПОИСК ПО НАЗВАНИЮ -->
        <v-col
          cols="12"
          class="pt-0 pb-2"
        >
          <v-text-field
            dense
            clearable
            solo
            :label="$t('filters.searchByName')"
            single-line
            prepend-inner-icon="fas fa-search"
            hide-details
            :value="filters.search"
            @input="search"
          />
        </v-col>
      </v-row>
    </v-col>
  </v-row>
</template>

<script>
import {mapGetters}         from 'vuex';

import TopbarActions        from './GroupsTopbarActions.vue';

export default {
  name: 'GroupsTopbar',
  
  components: {
    TopbarActions
  },

  data() {
    return {
      searchText: '',
    };
  },

  computed: {
    ...mapGetters({
      // dialogs: 'upload_lists/dialogs',
      // loading: 'upload_lists/loading',
      filters: 'groups/filters',
    }),
  },

  methods: {
    search(search) {
      this.searchText = search;
      const data = {
        filter: 'search',
        data: search
      };
      setTimeout(async () => {
        if (search === this.searchText) {
          await this.$store.dispatch('groups/setSpecificFilter', data);
        }
      }, 500);
    },

    async createRulesGroup() {
      await this.$store.dispatch('groups/setRulesGroupDialogMode', 'create');
      this.$store.dispatch('groups/openDialog', 'rulesGroupDialog');
    }
  }
};
</script>