<template>
  <div>
    <v-card>
      <v-card-text style="padding: 0px 8px 0px 8px">
        <!-- ТАБЛИЦА -->
        <v-data-table
          :value="rules.selected"
          :headers="cols"
          :items="rules.filtered"
          :loading="loading.mainTable"
          :loading-text="$t('common.loading')"
          fixed-header
          :height="innerHeight - 50 - 48 - 36"
          :page.sync="page"
          :items-per-page="itemsPerPage"
          :custom-sort="customSortsRules"
          hide-default-footer
          :mobile-breakpoint="0"
          :header-props="{'sort-icon': 'arrow_downward' }"
          show-select
          @page-count="pageCount = $event"
          @input="selectItem"
          @update:items-per-page="setItemsPerPage($event)"
          @update:page="updatePage"
        >
          <!-- НАЗВАНИЕ -->
          <template #item.name="{item}">
            <main-table-name :item="item" />
          </template>

          <!-- ДАННЫЕ -->
          <template #item.data="{item}">
            <main-table-data :item="item" />
          </template>

          <!-- УСЛОВИЯ -->
          <template #item.conditions="{item}">
            <main-table-conditions :item="item" />
          </template>
        </v-data-table>

        <!-- ФУТЕР ТАБЛИЦЫ -->
        <div
          class="d-flex justify-end align-center dolphin-main-table-footer"
          :class="{'justify-center': $vuetify.breakpoint.xs || $vuetify.breakpoint.sm, 'justify-space-between': !$vuetify.breakpoint.xs && !$vuetify.breakpoint.sm}"
        >
          <!-- ТЕГИ -->
          <div
            v-if="!$vuetify.breakpoint.xs && !$vuetify.breakpoint.sm"
            style="overflow: hidden; white-space: nowrap; overflow-x: auto; scroll-direction: horizontal; width: auto; margin-right: auto;"
            class="dolphin-main-table-footer-tags"
          />

          <!-- ПАГИНАЦИЯ -->
          <div
            style="white-space: nowrap;"
            class="mx-2"
          >
            {{ currentTableItems + ' ' + $t('common.of').toLowerCase() + ' ' + itemsCount }}
          </div>

          <div style="white-space: nowrap;">
            <!-- СТРЕЛКА ВЛЕВО -->
            <v-btn
              icon
              x-small
              :disabled="page === 1"
              @click="changePage(false)"
            >
              <v-icon :size="12">
                fas fa-chevron-left
              </v-icon>
            </v-btn>

            <!-- СТРАНИЦА -->
            <v-chip
              label
              color="primary"
              small
              class="mx-2"
            >
              {{ page }}
            </v-chip>

            <!-- СТРЕЛКА ВПРАВО -->
            <v-btn
              icon
              x-small
              :disabled="page === pageCount"
              @click="changePage(true)"
            >
              <v-icon :size="12">
                fas fa-chevron-right
              </v-icon>
            </v-btn>
          </div>

          <v-select
            :value="itemsPerPage"
            :items="[10, 30, 50, 100, -1]"
            hide-details
            dense
            solo
            class="dolphin-main-table-footer-items-per-page"
            :class="{'dolphin-main-table-footer-items-per-page-mobile': $vuetify.breakpoint.sm || $vuetify.breakpoint.xs}"
            style="max-width: 60px;"
            @change="setItemsPerPage($event)"
          >
            <template #item="{item}">
              {{ item === -1 ? $t('common.all') : item }}
            </template>
            <template #selection="{item}">
              {{ item === -1 ? $t('common.all') : item }}
            </template>
          </v-select>
        </div>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import customSort                                   from '@/mixins/adsmanager/custom_sort';
import {mapGetters}                                 from 'vuex';

import MainTableConditions                          from './RulesMainTableConditions.vue';
import MainTableData                                from './RulesMainTableData.vue';
import MainTableName                                from './RulesMainTableName.vue';

export default {
  name: 'RulesMainTable',

  components: {
    MainTableConditions,
    MainTableData,
    MainTableName
  },

  mixins: [
    customSort,
  ],

  data() {
    return {
      cols: [
        {
          text: this.$t('common.name'),
          value: 'name',
          width: 300,
        },
        {
          text: this.$t('common.data'),
          value: 'data',
          width: 300,
        },
        {
          text: this.$t('autoRules.fbRules.static.conditions'),
          value: 'conditions',
          width: 200,
        }
      ],
      itemsPerPage: 50,
    };
  },

  computed: {
    ...mapGetters({
      rules: 'rules/rules',
      selected: 'rules/selected',
      innerHeight: 'main/innerHeight',
      loading: 'rules/loading',
      filters: 'rules/filters',
      pageCount: 'rules/count',
      itemsCount: 'rules/itemsCount',
      page: 'rules/page',
      profile: 'main/profile',
    }),

    currentTableItems() {
      if (this.itemsPerPage === -1 || this.itemsPerPage > this.rules.filtered.length) {
        return this.rules.filtered.length;
      }
      if (this.page === 1) {
        return `1 - ${this.itemsPerPage}`;
      }
      if (this.page === this.pageCount) {
        return `${(this.page * this.itemsPerPage) - (this.itemsPerPage - 1)} - ${this.rules.filtered.length}`;
      }
      return `${(this.page * this.itemsPerPage) - (this.itemsPerPage - 1)} - ${this.page * this.itemsPerPage}`;
    },

  },
  created() {
    this.$store.dispatch('rules/loadRules');
  },

  methods: {
    selectItem(item) {
      this.$store.dispatch('rules/setSelected', item);
    },

    setItemsPerPage(number) {
      setTimeout(() => {
        this.setWidths('Rules');
      }, 100);

      localStorage.setItem('autorules-per-page-count', number);
      localStorage.setItem('autorules-page', '1');
      this.$store.dispatch('rules/loadRules');
      this.itemsPerPage = number;
      this.page = 1;
    },

    updatePage() {
      setTimeout(() => {
        this.setWidths('Rules');
      }, 100);
    },

    changePage(next) {
      let page = 1;
      if(next) {
        page = this.page = this.page + 1;
      } else {
        page = this.page = this.page - 1;
      }
      this.$store.dispatch('rules/setPage', page);
      this.$store.dispatch('rules/loadRules');
    },
  }
};
</script>
