<template>
  <div>
    <!-- МАССОВЫЕ ДЕЙСТВИЯ - ДУБЛИРОВАТЬ -->
    <div 
      class="pt-0 pb-2"
      style="float: left;"
    >
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="rules.selected.length === 0"
            color="primary"
            text
            style="min-width: 0; width: 30px; margin-top: 1px;"
            @click="multipleDuplicate"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-copy
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.duplicate') }}</span>
      </v-tooltip>
    </div>

    <!-- КНОПКА УДАЛИТЬ -->
    <div
      class="pt-0 pb-2"
      style="float: left;"
    >
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="rules.selected.length === 0"
            color="primary"
            text
            style="min-width: 0; width: 30px; margin-top: 1px;"
            :loading="loading.deleteRule"
            @click="deleteRule"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-trash
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.delete') }}</span>
      </v-tooltip>
    </div>

    <!-- КНОПКА ПЕРЕДАТЬ -->
    <div
      class="pt-0 pb-2"
      style="float: left;"
    >
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="rules.selected.length === 0"
            color="primary"
            text
            style="min-width: 0; width: 30px; margin-top: 1px;"
            :loading="loading.shareRule"
            @click="shareRule"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-share-alt
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.share') }}</span>
      </v-tooltip>
    </div>

    <!-- ИНДИКАТОР -->
    <div
      class="pt-0 pb-2"
      style="float: left;"
    >
      <div
        class="body-2 ml-3"
        style="padding-bottom: 3px;"
        :style="{color: rules.selected.length > 0 ? '' : 'gray', paddingTop: '5px'}"
      >
        {{ $t('common.selected') }}: {{ rules.selected.length }}
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'RulesTopbarActions',

  computed: {
    ...mapGetters({
      loading: 'rules/loading',
      rules: 'rules/rules',
    })
  },
  methods: {
    deleteRule() {
      if (!confirm(this.$t('common.confirmPlease'))) return;
      let ids = this.rules.selected.map(rule => rule.id);
      this.$store.dispatch('rules/deleteRule', ids);
    },

    shareRule() {
      this.$store.dispatch('rules/openDialog', 'share');
    },

    async multipleDuplicate() {
      if (!confirm(this.$t('common.confirmPlease'))) return;
      let data = {
        param: 'mainTable',
        value: true
      };
      await this.$store.dispatch('rules/setLoading', data);
      for (let rule of this.rules.selected) {
        await this.$store.dispatch('rules/multipleDuplicate', rule.id);
      }
      data = {
        param: 'mainTable',
        value: false
      };
      await this.$store.dispatch('rules/setLoading', data);      
      this.$store.dispatch('rules/loadRules');
      this.$store.dispatch('rules/clearSelected');
    },
  }
};
</script>