<template>
  <div>
    <span
      style="cursor: pointer; color: #2196F3; text-decoration: underline"
      @click="editRule(item)"
    >
      {{ item.name }}
    </span>
  </div>
</template>

<script>

export default {
  name: 'RulesMainTableName',

  props: {
    item: {
      type: Object,
      default: () => ({})
    }
  },

  methods: {
    async editRule(rule) {
      await this.$store.dispatch('rules/setMainDialogMode', 'edit');
      
      const ruleCloned = this.$_.cloneDeep(rule);
      ruleCloned.convert_currency = ruleCloned.convert_currency === 1;
      ruleCloned.conditions.forEach(condition => {
        if (condition.value === '') {
          condition.value = null;
        }
      });

      await this.$store.dispatch('rules/setForMainDialog', ruleCloned);
      this.$store.dispatch('rules/openDialog', 'mainDialog');
    }
  }
};
</script>