<template>
  <div class="py-2">
    <!-- ЧИП ЧТО ДЕЛАТЬ - ОСТАНОВИТЬ -->
    <div v-if="item.execution_type === 'PAUSE'">
      <v-chip
        class="mr-1"
        color="red"
        outlined
        small
        label
      >
        {{ $t(`autoRules.fbRules.static.whatToDoType.${item.execution_type.toLowerCase()}`) + ' ' + $t(`autoRules.fbRules.static.whatToWorkWithType.${item.entity_type.toLowerCase()}`).toLowerCase() }}
      </v-chip>
    </div>

    <!-- ЧИП ЧТО ДЕЛАТЬ - ЗАПУСТИТЬ -->
    <div v-if="item.execution_type === 'UNPAUSE'">
      <v-chip
        class="mr-1"
        color="green"
        outlined
        small
        label
      >
        {{ $t(`autoRules.fbRules.static.whatToDoType.${item.execution_type.toLowerCase()}`) + ' ' + $t(`autoRules.fbRules.static.whatToWorkWithType.${item.entity_type.toLowerCase()}`).toLowerCase() }}
      </v-chip>
    </div>

    <!-- ЧИП ЧТО ДЕЛАТЬ - ЗАПУСТИТЬ -->
    <div v-if="item.execution_type === 'CHANGE_BUDGET' || item.execution_type === 'CHANGE_CAMPAIGN_BUDGET'">
      <v-chip
        class="mr-1"
        :color="item.execution.budget_change_amount > 0 ? 'green' : 'red'"
        outlined
        small
        label
      >
        {{ 
          ( profile.language === 'en' ? 
            (
              ( item.execution.budget_change_amount > 0 ? $t(`common.increase`) : $t(`common.decrease`) ) + ' '+
              ( item.budget_type === 'daily' ? $t('common.daily').toLowerCase() + ' ' : $t('common.lifetime').toLowerCase() + ' ' ) +
              $t('common.budget').toLowerCase() + ' ' 
            ) : 
            ( (item.execution.budget_change_amount > 0 ? $t(`autoRules.fbRules.static.budgetIncrease`) : $t(`autoRules.fbRules.static.budgetDecrease`) ) +
              ' (' + ( item.budget_type === 'daily' ? $t('common.daily').toLowerCase() : $t('common.lifetime').toLowerCase() ) + ') '
            ) 
          ) +
            $t(`autoRules.fbRules.static.whatToWorkWithType.${item.entity_type === "AD" ? 'ofAd' : item.entity_type === "ADSET" ? 'ofAdset' : 'ofCampaign' }`).toLowerCase() + ' ' + 
            $t('autoRules.fbRules.static.by').toLowerCase() + ' ' + 
            (item.execution.budget_change_amount || 0) + '%'
        }}
      </v-chip>
    </div>

    <!-- ПЕРИОД СТАТЫ -->
    <div>
      {{ $t('autoRules.fbRules.static.statPeriod') + ': ' + $t(`autoRules.fbRules.static.statPeriods.${item.time_preset.toLowerCase()}`) }}
    </div>

    <!-- ЧАСТОТА -->
    <div>
      {{ $t('autoRules.fbRules.static.frequency') + ': ' + $t(`autoRules.fbRules.static.frequencyTypes.${item.schedule_type.toLowerCase()}`) }}
    </div>
  </div>
</template>

<script>
import {mapGetters}   from 'vuex';

export default {
  name: 'RulesMainTableData',

  props: {
    item: {
      type: Object,
      default: () => ({})
    }
  },

  computed: {
    ...mapGetters({
      profile: 'main/profile'
    })
  },
};
</script>