<template>
  <div style="padding: 8px;">
    <rules-group-dialog v-if="dialogs.rulesGroupDialog" />
    <dialog-share v-if="dialogs.share" />
    <topbar />
    <main-table />
  </div>
</template>

<script>
import DialogShare                          from '@/components/autorules/groups/GroupsDialogShare.vue';
import MainTable                            from '@/components/autorules/groups/GroupsMainTable.vue';
import RulesGroupDialog                     from '@/components/autorules/groups/GroupsRulesGroupDialog.vue';
import Topbar                               from '@/components/autorules/groups/GroupsTopbar.vue';
import {mapGetters}                         from 'vuex';

export default {
  name: 'Groups',

  components: {
    MainTable,
    Topbar,
    RulesGroupDialog,
    DialogShare
  },

  data() {
    return {
      updateInterval: null
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'groups/dialogs',
    })
  },

  created() {
    this.$store.dispatch('groups/loadGroups');
    this.$store.dispatch('groups/loadFilters');
    this.$store.dispatch('groups/loadRules');
    this.updateInterval = setInterval(() => {
      this.$store.dispatch('groups/loadGroups');
    }, 60000);
  },

  beforeDestroy() {
    clearInterval(this.updateInterval);
  },
};
</script>