<template>
  <v-col cols="12">
    <v-row>
      <!-- ТИП БЮДЖЕТА -->
      <v-col
        :cols="12"
        class="py-0 mb-2"
        style="text-align: left;"
        :style="$vuetify.breakpoint.xs ? 'padding-left: 0 !important;' : ''"
      >
        <v-btn-toggle
          v-model="localRule.budget_type"
          color="primary"
          borderless
          mandatory
          class="py-2"
        >
          <v-btn 
            value="daily"
            style="font-size: 12px; height: 24px;"
          >
            <span>{{ $t('common.budgetDaily') }}</span>
          </v-btn>

          <v-btn 
            value="lifetime"
            style="font-size: 12px; height: 24px;"
          >
            <span>{{ $t('common.budgetLifetime') }}</span>
          </v-btn>
        </v-btn-toggle>
      </v-col>

      <!-- ОПЕРАТОР БЮДЖЕТА -->
      <v-col
        :cols="12"
        :sm="3"
        class="py-0"
        style="text-align: left;"
        :style="$vuetify.breakpoint.xs ? 'padding-left: 0 !important;' : ''"
      >
        <v-btn-toggle
          v-model="localRule.execution.budget_operator"
          color="primary"
          borderless
          mandatory
          class="py-2"
        >
          <v-btn 
            value="+"
            style="font-size: 12px; height: 24px;"
          >
            <span>+</span>
          </v-btn>

          <v-btn 
            value="-"
            style="font-size: 12px; height: 24px;"
          >
            <span>-</span>
          </v-btn>
        </v-btn-toggle>
      </v-col>

      <!-- ПРОЦЕНТЫ БЮДЖЕТА -->
      <v-col
        :cols="12"
        :sm="3"
        class="py-0 pl-0"
      >
        <v-text-field
          :value="localRule.execution.budget_percents"
          maxlength="4"
          outlined
          dense
          hide-details
          append-icon="fas fa-percent"
          :error="!localRule.execution.budget_percents && showWarnings"
          @input="budgetPercentsChanged($event)"
        />
      </v-col>
      <v-col
        :cols="12"
        :sm="6"
        class="py-0"
        :style="$vuetify.breakpoint.xs ? 'margin-top: 16px; padding-left: 0 !important;' : ''"
      >
        <!-- МАКСИМАЛЬНЫЙ БЮДЖЕТ -->
        <v-text-field
          v-show="localRule.execution.budget_operator === '+'"
          :value="localRule.execution.budget_max"
          :label="$t('autoRules.fbRules.static.budgetMax')"
          outlined
          dense
          hide-details
          @input="budgetMax($event)"
        />

        <!-- МИНИМАЛЬНЫЙ БЮДЖЕТ -->
        <v-text-field
          v-show="localRule.execution.budget_operator === '-'"
          :value="localRule.execution.budget_min"
          :label="$t('autoRules.fbRules.static.budgetMin')"
          outlined
          dense
          hide-details
          @input="budgetMin($event)"
        />
      </v-col>
    </v-row>
  </v-col>
</template>

<script>
import { mapFields }        from 'vuex-map-fields';
import {mapGetters}         from 'vuex';

export default {
  name: 'RulesMainDialogBudget',

  props: {
    localRule: {
      type: Object,
      default: () => ({})
    }
  },

  data() {
    return {
      budgetOperator: ['+', '-']
    };
  },

  computed: {
    ...mapGetters({
      showWarnings: 'rules/showWarnings'
    }),
    ...mapFields('rules', ['rules']),
  },

  created () {
    if (this.localRule.execution.budget_change_amount) {
      this.$store.dispatch('rules/setForMainDialogExecutionOptions', {option: 'budget_percents', value: this.localRule.execution.budget_change_amount});
    }
  },

  methods: {
    budgetPercentsChanged(event) {
      let budgetPercents = parseFloat(event);

      if (isFinite(budgetPercents) && budgetPercents > 1000) {
        budgetPercents = 1000;
      } else if (!isFinite(budgetPercents)) {
        budgetPercents = null;
      }

      this.rules.forMainDialog.execution.budget_percents = budgetPercents;
    },

    budgetMax(event) {
      this.localRule.execution.budget_max = event.toString().trim().replace(',', '.');
    },
    budgetMin(event) {
      this.localRule.execution.budget_min = event.toString().trim().replace(',', '.');
    }
  }
};
</script>