<template>
  <v-col
    :cols="12"
    :sm="6"
  >
    <!-- ЧТО ДЕЛАТЬ? -->
    <v-select
      v-model="localRule.execution_type"
      :label="$t('autoRules.fbRules.static.whatToDo')"
      :item-text="item => $t(`autoRules.fbRules.static.whatToDoType.${item.text}`)"
      :item-value="item => item.value"
      :items="executionType"
      outlined
      dense
      hide-details
    />
  </v-col>
</template>

<script>
import executionType                                 from '@/constants/autorules/execution_type.js';

export default {
  name: 'RulesMainDialogWhatToDo',

  props: {
    localRule: {
      type: Object,
      default: () => ({})
    }
  },

  data() {
    return {
      executionType
    };
  },

  watch: {
    'localRule.execution_type': {
      handler() {
        if (this.localRule.execution_type === 'CHANGE_BUDGET') {
          this.$store.dispatch('rules/setForMainDialogOptions', {option: 'entity_type', value: 'ADSET'});
        }
        if (this.localRule.execution_type === 'CHANGE_CAMPAIGN_BUDGET') {
          this.$store.dispatch('rules/setForMainDialogOptions', {option: 'entity_type', value: 'CAMPAIGN'});
        }
      }
    }
  },
};
</script>