<template>
  <div>
    <span
      style="cursor: pointer; color: #2196F3; text-decoration: underline"
      @click="editGroup(item)"
    >
      {{ item.name }}
    </span>
  </div>
</template>

<script>

export default {
  name: 'GroupsMainTableName',

  props: {
    item: {
      type: Object,
      default: () => ({})
    }
  },

  methods: {
    async editGroup(group) {
      let data = {
        id: group.id,
        name: group.name,
        rules: group.rules.map(rule => rule.id)
      };
      await this.$store.dispatch('groups/setRulesGroupDialogMode', 'edit');
      await this.$store.dispatch('groups/setGroup', {...data});
      this.$store.dispatch('groups/openDialog', 'rulesGroupDialog');
    }
  }
};
</script>