<template>
  <div style="padding: 8px;">
    <main-dialog v-if="dialogs.mainDialog" />
    <dialog-share v-if="dialogs.share" />
    <topbar />
    <main-table />
  </div>
</template>

<script>
import DialogShare                    from '@/components/autorules/rules/RulesDialogShare.vue';
import MainDialog                     from '@/components/autorules/rules/RulesMainDialog.vue';
import MainTable                      from '@/components/autorules/rules/RulesMainTable.vue';
import Topbar                         from '@/components/autorules/rules/RulesTopbar.vue';
import {mapGetters}                   from 'vuex';

export default {
  name: 'Rules',

  components: {
    MainDialog,
    MainTable,
    DialogShare,
    Topbar
  },

  data() {
    return {
      updateInterval: null
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'rules/dialogs',
    })
  },

  created() {
    this.$store.dispatch('rules/loadFilters');
    this.$store.dispatch('rules/loadRules');
    this.updateInterval = setInterval(() => {
      this.$store.dispatch('rules/loadRules');
    }, 60000);
  },

  beforeDestroy() {
    clearInterval(this.updateInterval);
  },
};
</script>
