<template>
  <div>
    <!-- МАССОВЫЕ ДЕЙСТВИЯ - ДУБЛИРОВАТЬ -->
    <div 
      class="pt-0 pb-2"
      style="float: left;"
    >
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="groups.selected.length === 0"
            color="primary"
            text
            style="min-width: 0; width: 30px; margin-top: 1px;"
            @click="multipleDuplicate"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-copy
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.duplicate') }}</span>
      </v-tooltip>
    </div>

    <!-- КНОПКА УДАЛИТЬ -->
    <div
      class="pt-0 pb-2"
      style="float: left;"
    >
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="groups.selected.length === 0"
            color="primary"
            text
            style="min-width: 0; width: 30px; margin-top: 1px;"
            :loading="loading.deleteGroup"
            @click="deleteGroup"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-trash
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.delete') }}</span>
      </v-tooltip>
    </div>

    <!-- ПОДЕЛИТЬСЯ -->
    <div
      class="pt-0 pb-2"
      style="float: left;"
    >
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="groups.selected.length === 0"
            color="primary"
            text
            style="min-width: 0; width: 30px; margin-top: 1px;"
            :loading="loading.shareGroup"
            @click="shareGroup"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-share-alt
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.share') }}</span>
      </v-tooltip>
    </div>

    <!-- ИНДИКАТОР -->
    <div
      class="pt-0 pb-2"
      style="float: left;"
    >
      <div
        class="body-2 ml-3"
        style="padding-bottom: 3px;"
        :style="{color: groups.selected.length > 0 ? '' : 'grey', paddingTop: '5px'}"
      >
        {{ $t('common.selected') }}: {{ groups.selected.length }}
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'GroupsTopbarActions',

  computed: {
    ...mapGetters({
      loading: 'groups/loading',
      groups: 'groups/groups',
    })
  },
  methods: {
    deleteGroup() {
      if (!confirm(this.$t('common.confirmPlease'))) return;

      this.$store.dispatch(
          'groups/deleteGroup',
          { ids: this.groups.selected.map(group => group.id) }
      );
    },
    
    shareGroup() {
      this.$store.dispatch('groups/openDialog', 'share');
    },

    async multipleDuplicate() {
      if (!confirm(this.$t('common.confirmPlease'))) return;
      for (let group of this.groups.selected) {
        await this.$store.dispatch('groups/multipleDuplicate', group.id);
      }    
      this.$store.dispatch('groups/loadGroups');
      this.$store.dispatch('groups/clearSelected');
    },
  }
};
</script>