<template>
  <v-tabs
    centered
    grow
    :value="tabNumber"
    :height="32"
  >
    <!-- ПРАВИЛА FACEBOOK -->
    <v-tab
      key="tab-rules"
      :value="0"
      @click="selectTab('rules')"
    >
      {{ $t('autoRules.tabs.facebookRules') }}
    </v-tab>

    <!-- ГРУППЫ ПРАВИЛ -->
    <v-tab 
      key="tab-groups"
      :value="1"
      @click="selectTab('groups')"
    >
      {{ $t('autoRules.tabs.rulesGroups') }}
    </v-tab>

    <!-- ПРАВЛО ПО ROI -->
    <v-tab
      v-show="false"
      key="tab-roi"
      @click="selectTab('roi')"
    >
      {{ $t('autoRules.tabs.roiBasedRules') }}
    </v-tab>
  </v-tabs>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'AutoRulesTabs',

  data: () => ({
    tabs: ['rules', 'groups', 'roi'],
  }),

  computed: {
    ...mapGetters({
      activeTab: 'autorules/activeTab',
    }),

    tabNumber() {
      if (typeof this.$route.params.tab === 'undefined' || this.$route.params.tab === 'rules') {
        return 0;
      } else if (this.$route.params.tab === 'groups') {
        return 1; 
      } else if (this.$route.params.tab === 'roi') {
        return 2; 
      }
      return 0;
    }
  },

  methods: {
    changeTab(tab) {
      this.$store.dispatch('autorules/setActiveTab', {
        tab: tab,
        component: this.tabs[tab]
      });
    },

    selectTab(tab) {
      return this.$route.params.tab === tab ? true : this.$router.push({path: `/fbrules/${tab}`, params: {tab: tab}});
    }
  }
};
</script>