<template>
  <div>
    <tabs />
    <div>
      <component :is="componentName" />
    </div>
  </div>
</template>

<script>
import Tabs             from '@/components/autorules/AutoRulesTabs.vue';
import Groups           from '@/views/autorules/Groups.vue';
import Roi              from '@/views/autorules/Roi.vue';
import Rules            from '@/views/autorules/Rules.vue';

export default {
  name: 'AutoRules',

  components: {
    Tabs,
    Rules,
    Groups,
    Roi
  },

  computed: {
    componentName() {
      if (typeof this.$route.params.tab !== 'undefined') {
        return this.$route.params.tab;
      }
      return 'rules';
    }
  }
};
</script>