<template>
  <v-col
    :cols="12"
    class="px-0"
    style="display: flex; flex-direction: column; justify-content: center; align-items: center;"
  >
    <!-- УСЛОВИЯ -->
    <div
      v-if="localRule.conditions"
      style="width: 100%;"
    >
      <div
        v-for="(condition, conditionIndex) in localRule.conditions.length"
        :key="`rules-conditions-${condition}`"
        class="d-flex align-center"
        :style="$vuetify.breakpoint.xs ? 'flex-wrap: wrap; justify-content: space-between !important;' : ''"
      >
        <!-- ПАРАМЕТР -->
        <v-autocomplete
          v-model="localRule.conditions[conditionIndex].parameter"
          :item-text="item => item.value.startsWith('title-') || item.value.startsWith('divider-') ? null : $t(`autoRules.fbRules.static.parameter.${item.text}`)"
          :item-value="item => item.value"
          :items="computedConditionsParameter"
          outlined
          dense
          hide-details
          :menu-props="{ auto: true }"
          class="my-2"
          style="width: 100%;"
          :error="!localRule.conditions[conditionIndex].parameter && showWarnings"
          @change="parameterChanged"
        >
          <template #selection="data">
            {{ $t(`autoRules.fbRules.static.parameter.${data.item.text}`) }}
          </template>

          <template #item="data">
            <v-list-item-content>
              <v-divider v-if="data.item.divider" />

              <v-list-item-title
                v-else
                :style="data.item.value.startsWith('title-') ? 'font-size: 16px;' : null"
              >
                {{ $t(`autoRules.fbRules.static.parameter.${data.item.text}`) }}
              </v-list-item-title>
            </v-list-item-content>
          </template>
        </v-autocomplete>

        <!-- ОПЕРАТОР -->
        <v-select
          v-model="localRule.conditions[conditionIndex].operator"
          :item-text="item => item.text"
          :item-value="item => item.value"
          :items="conditionsOperator"
          outlined
          dense
          hide-details
          class="mx-4 my-2"
          style="max-width: 100px;"
          :style="$vuetify.breakpoint.xs ? 'max-width: 40%; margin: 0 12px 0 0 !important;' : ''"
        />

        <!-- ЗНАЧЕНИЕ -->
        <v-text-field
          :value="localRule.conditions[conditionIndex].value"
          outlined
          dense
          hide-details
          class="my-2"
          :error="!localRule.conditions[conditionIndex].value && showWarnings"
          :style="$vuetify.breakpoint.xs ? 'max-width: 40%; !important;' : ''"
          @input="valueChanged($event, conditionIndex)"
        />

        <!-- УДАЛИТЬ УСЛОВИЯ -->
        <v-tooltip bottom>
          <template v-slot:activator="{ on }">
            <v-btn
              small
              color="red"
              text
              style="min-width: 0; width: 30px;"
              @click="deleteCondition(conditionIndex)"
              v-on="on"
            >
              <v-icon :size="12">
                fas fa-trash
              </v-icon>
            </v-btn>
          </template>
          <span>{{ $t('common.delete') }}</span>
        </v-tooltip>
      </div>
    </div>
    <!-- ДОБАВИТЬ УСЛОВИЕ -->
    <v-btn
      small
      text
      color="primary"
      class="white--text"
      style="align-content: center;"
      @click="addCondition"
    >
      {{ $t('autoRules.createCondition') }}
    </v-btn>

    <!-- БЕЗ УСЛОВИЙ -->
    <v-btn
      v-if="checkConditions"
      small
      text
      color="primary"
      class="white--text"
      style="align-content: center;"
      @click="noConditionsRule"
    >
      {{ $t('autoRules.noConditionsRule') }}
    </v-btn>
  </v-col>
</template>

<script>
import conditionsOperator                                  from '@/constants/autorules/conditions_operator.js';
import conditionsParameter                                 from '@/constants/autorules/conditions_parameter.js';
import {mapGetters}                                        from 'vuex';

export default {
  name: 'RulesMainDialogConditions',

  props: {
    localRule: {
      type: Object,
      default: () => ({})
    }
  },

  data() {
    return {
      conditionsParameter,
      conditionsOperator,
      selectedParameters: []
    };
  },
  
  computed: {
    ...mapGetters({
      showWarnings: 'rules/showWarnings'
    }),

    checkConditions() {
      for (let condition of this.localRule.conditions) {
        if (condition.value !== null && condition.value !== '') {
          return false;
        }
      }
      return true;
    },

    computedConditionsParameter() {
      const selectedParameters = this.localRule.conditions.map(item => item.parameter);

      return conditionsParameter.map((item, index) => {
        if (item.divider) {
          return {
            divider: true,
            text: 'divider',
            value: `divider-${index}`,
            disabled: true
          };
        }
        if (item.value === undefined) {
          return {
            text: item.text,
            value: `title-${item.text}`,
            disabled: true
          };
        }
        return {
          text: item.text,
          value: item.value,
          disabled: selectedParameters.includes(item.value)
        };
      });
    }
  },

  methods: {
    parameterChanged() {
      this.selectedParameters = this.localRule.conditions.map(item => item.parameter);
    },

    addCondition() {
      let newCondition = {
        // parameter: 'impressions', 
        operator: 'EQUAL',
        value: null
      };

      this.localRule.conditions.push(newCondition);
    },

    noConditionsRule() {
      if (this.localRule.conditions && this.localRule.conditions.length > 0) {
        this.localRule.conditions[0].operator = 'LESS_THAN';
        this.localRule.conditions[0].parameter = 'spent';
        this.localRule.conditions[0].value = '999999';
      } else {
        let newCondition = {
          parameter: 'spent', 
          operator: 'LESS_THAN',
          value: '999999'
        };

        this.localRule.conditions.push(newCondition);
      }
    },

    deleteCondition(conditionIndex) {
      this.localRule.conditions = this.localRule.conditions.filter((condition, index) => index !== conditionIndex);
    },

    valueChanged(event, conditionIndex) {
      if (event === '') {
        this.localRule.conditions[conditionIndex].value = null;
      } else {
        this.localRule.conditions[conditionIndex].value = event.toString().trim().replace(',', '.');
      }
    }
  }
};
</script>