<template>
  <v-dialog
    :value="dialogs.mainDialog"
    persistent
    fullscreen
    transition="dialog-bottom-transition"
    @keydown.esc="close"
  >
    <v-card>
      <!-- ХЕДЕР ДИАЛОГА -->
      <v-toolbar
        class="white--text main-dialog-header"
        color="primary"
        flat
      >
        <v-btn
          color="white"
          icon
          @click="close"
        >
          <v-icon>fas fa-times</v-icon>
        </v-btn>
        <v-toolbar-title>
          {{ $t(`autoRules.mainDialog.${mainDialogMode}`) }}
          <span v-show="mainDialogMode === 'edit'">
            {{ ' [' + rules.forMainDialog.name + ']' }}
          </span>
        </v-toolbar-title>
        <v-spacer />
        <v-toolbar-items>
          <v-btn
            v-if="($vuetify.breakpoint.sm || $vuetify.breakpoint.xs) ? false : true"
            class="white--text"
            text
            @click="close"
          >
            {{ $t('common.cancel') }}
          </v-btn>
          <v-btn
            class="white--text" 
            text
            :loading="loading.createOrSaveRule"
            @click="createOrSaveRule"
          >
            {{ 
              mainDialogMode == 'edit' 
                ? $t('common.save')
                : $t('common.create')
            }}
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>      
      <v-row class="autorules-main-dialog">
        <v-col
          cols="12"
          md="6"
          offset-md="3"
          xl="4"
          offset-xl="4"
        >
          <v-card class="ma-4 pa-2 px-5 pb-3">
            <v-card-title style="padding: 0;">
              {{ $t('autoRules.fbRules.static.cards.settings') }}
            </v-card-title>
            <!-- НАЗВАНИЕ -->
            <main-dialog-name :local-rule="rules.forMainDialog" />

            <!-- КОНВЕРТИРОВАТЬ ВАЛЮТУ -->
            <main-dialog-convert-currency :local-rule="rules.forMainDialog" />

            <v-row>
              <!-- ТИПА ПРАВИЛА -->
              <main-dialog-rule-type :local-rule="rules.forMainDialog" />

              <!-- ЧАСТОТА -->
              <main-dialog-frequency :local-rule="rules.forMainDialog" />
            </v-row>

            <v-row>
              <!-- С ЧЕМ РАБОТАТЬ? -->
              <main-dialog-what-to-work-with :local-rule="rules.forMainDialog" />

              <!-- КАКИЕ СТАТУСЫ? -->
              <main-dialog-which-statuses :local-rule="rules.forMainDialog" />
            </v-row>

            <v-row>
              <!-- ЧТО ДЕЛАТЬ? -->
              <main-dialog-what-to-do :local-rule="rules.forMainDialog" />

              <!-- ПЕРИОД СТАТЫ -->
              <main-dialog-stat-period :local-rule="rules.forMainDialog" />
            </v-row>
          </v-card>

          <!-- БЮДЖЕТ -->
          <v-card
            v-if="rules.forMainDialog.execution_type === 'CHANGE_BUDGET' || rules.forMainDialog.execution_type === 'CHANGE_CAMPAIGN_BUDGET'"
            class="ma-4 pa-2 px-5 pb-3"
          >
            <v-card-title style="padding: 0;">
              {{ $t('autoRules.fbRules.static.cards.budgetChange') }}
            </v-card-title>

            <main-dialog-budget
              :local-rule="rules.forMainDialog"
            />
          </v-card>

          <!-- СТАВКА -->
          <v-card
            v-if="rules.forMainDialog.execution_type === 'CHANGE_BID'"
            class="ma-4 pa-2 px-5 pb-3"
          >
            <v-card-title style="padding: 0;">
              {{ $t('autoRules.fbRules.static.cards.changeBid') }}
            </v-card-title>

            <main-dialog-bid
              :local-rule="rules.forMainDialog"
            />
          </v-card>

          <!-- КАСТОМНОЕ РАСПИСАНИЕ -->
          <v-card
            v-if="rules.forMainDialog.schedule_type === 'CUSTOM'"
            class="ma-4 pa-2 px-5 pb-3"
          >
            <v-card-title style="padding: 0;">
              {{ $t('autoRules.fbRules.static.cards.customSchedule') }}
            </v-card-title>

            <main-dialog-custom-schedule
              :local-rule="rules.forMainDialog"
            />
          </v-card>

          <!-- УСЛОВИЯ -->
          <v-card class="ma-4 pa-2 px-5 pb-3">
            <v-card-title style="padding: 0;">
              {{ $t('autoRules.fbRules.static.cards.condition') }}
            </v-card-title>

            <main-dialog-conditions :local-rule="rules.forMainDialog" />
          </v-card>
        </v-col>
      </v-row>
    </v-card>
  </v-dialog>
</template>

<script>
import {mapGetters}                                       from 'vuex';

import MainDialogBid                                      from './RulesMainDialogBid.vue';
import MainDialogBudget                                   from './RulesMainDialogBudget.vue';
import MainDialogConditions                               from './RulesMainDialogConditions.vue';
import MainDialogConvertCurrency                          from './RulesMainDialogConvertCurrency.vue';
import MainDialogCustomSchedule                           from './RulesMainDialogCustomSchedule.vue';
import MainDialogFrequency                                from './RulesMainDialogFrequency.vue';
import MainDialogName                                     from './RulesMainDialogName.vue';
import MainDialogRuleType                                 from './RulesMainDialogRuleType.vue';
import MainDialogStatPeriod                               from './RulesMainDialogStatPeriod.vue';
import MainDialogWhatToDo                                 from './RulesMainDialogWhatToDo.vue';
import MainDialogWhatToWorkWith                           from './RulesMainDialogWhatToWorkWith.vue';
import MainDialogWhichStatuses                            from './RulesMainDialogWhichStatuses.vue';

export default {
  name: 'RulesMainDialog',

  components: {
    MainDialogBudget,
    MainDialogBid,
    MainDialogConditions,
    MainDialogConvertCurrency,
    MainDialogCustomSchedule,
    MainDialogName,
    MainDialogRuleType,
    MainDialogFrequency,
    MainDialogStatPeriod,
    MainDialogWhatToDo,
    MainDialogWhatToWorkWith,
    MainDialogWhichStatuses
  },

  data() {
    return {
      initial: {}
    };
  },

  computed: {
    ...mapGetters({
      rules: 'rules/rules',
      dialogs: 'rules/dialogs',
      mainDialogMode: 'rules/mainDialogMode',
      loading: 'rules/loading',
    })
  },

  watch: {
    'rules.forMainDialog': {
      deep: true,
      handler() {
        this.$store.dispatch('rules/setWarning', false);
      }
    }
  },

  created() {
    this.initial = this.$_.cloneDeep(this.rules.forMainDialog);
  },
  
  methods: {
    async createRule() {
      await this.$store.dispatch('rules/createRule');
      this.$store.dispatch('rules/closeDialog', 'mainDialog');
    },

    close() {
      if (!this.$_.isEqual(this.rules.forMainDialog, this.initial)) {
        this.confirmation = confirm(this.$t('common.closeDialogConfirmation'));
        if (!this.confirmation) return;
      }

      this.$store.dispatch('rules/closeDialog', 'mainDialog');
      this.$store.dispatch('rules/setWarning', false);
      this.$store.dispatch('rules/clearRule');
    },

    createOrSaveRule() {
      let conditionsCorrect = true;
      this.rules.forMainDialog.conditions.forEach(condition => {
        if (!condition.value) {
          conditionsCorrect = false;
        }
        if (!condition.parameter) {
          conditionsCorrect = false;
        }
      });

      let scheduleCorrect = true;
      if (this.rules.forMainDialog.schedule_type === 'CUSTOM' && 
        (!this.rules.forMainDialog.custom_rasp.days || this.rules.forMainDialog.custom_rasp.days.length === 0)
      ) {
        scheduleCorrect = false;
      }

      let executionTypeCorrect = true;
      if (this.rules.forMainDialog.execution_type === 'CHANGE_BUDGET' ||
        this.rules.forMainDialog.execution_type === 'CHANGE_CAMPAIGN_BUDGET') {
        if (!this.rules.forMainDialog.execution.budget_percents) {
          executionTypeCorrect = false;
        }
      }

      if (this.rules.forMainDialog.name &&
           conditionsCorrect &&
           scheduleCorrect &&
           executionTypeCorrect
      ) {
        if (this.mainDialogMode === 'edit') {
          this.$store.dispatch('rules/editRule');
        } else {
          this.$store.dispatch('rules/createRule');
        }
        return this.$store.dispatch('rules/setWarning', false);
      } else {
        return this.$store.dispatch('rules/setWarning', true);
      }
    }
  }
};
</script>
<style>
.main-dialog-header {
  position: fixed;
  top: 0; 
  left: 0;
  width: 100%;
  height: 48px !important;
  z-index: 2;  
}
.main-dialog-header .v-toolbar__content{
  height: 48px !important;
}
.autorules-main-dialog {
  margin-top: 48px; 
}
</style>