<template>
  <div>
    <v-card>
      <v-card-text style="padding: 0px 8px 0px 8px">
        <!-- ТАБЛИЦА -->
        <v-data-table
          :value="groups.selected"
          :headers="cols"
          :items="groups.filtered"
          :loading-text="$t('common.loading')"
          fixed-header
          :items-per-page="50"
          :footer-props="{'items-per-page-options': [10, 30, 50, 100, -1]}"
          :height="innerHeight - 50 - 48 - 36"
          show-select
          @input="selectItem"
        >
          <!-- НАЗВАНИЕ -->
          <template #item.name="{item}">
            <main-table-name :item="item" />
          </template>

          <!-- ПРАВИЛА -->
          <template #item.rules="{item}">
            <main-table-rules :item="item" />
          </template>
        </v-data-table>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import {mapGetters}                                  from 'vuex';

import MainTableName                                 from './GroupsMainTableName.vue';
import MainTableRules                                from './GroupsMainTableRules.vue';

export default {
  name: 'GroupsMainTable',

  components: {
    MainTableName,
    MainTableRules
  },

  data() {
    return {
      cols: [
        {
          text: this.$t('common.name'),
          value: 'name',
          width: 300,
        },
        {
          text: this.$t('common.rules'),
          value: 'rules',
          width: 300,
        }
      ]
    };
  },

  computed: {
    ...mapGetters({
      groups: 'groups/groups',
      innerHeight: 'main/innerHeight',
    })
  },

  methods: {
    selectItem(item) {
      this.$store.dispatch('groups/setSelected', item);
    }
  }

};
</script>