<template>
  <div>
    <!-- МОДАЛЬНОЕ ОКНО -->
    <v-dialog
      :value="dialogs.rulesGroupDialog"
      max-width="500px"
      scrollable
      @click:outside="close"
      @keydown.esc="close"
    >
      <v-card>
        <!-- ЗАГОЛОВОК МОДАЛЬНОГО ОКНА -->
        <v-card-title>
          <span>
            {{ rulesGroupDialogMode === 'create' ? $t('autoRules.createRulesGroup') : $t('autoRules.ediGroup') + ' ' + groupName }}
          </span>
        </v-card-title>
        <v-card-text style="max-height: 700px;">
          <!-- НАЗВАНИЕ -->
          <v-text-field
            :value="group.name"
            :error="!group.name && showWarnings"
            class="pt-2" 
            :label="$t('common.name')"
            outlined
            dense
            hide-details
            @input="changeGroupName($event)"
          />

          <!-- ПРАВИЛА -->
          <v-combobox
            :value="localRules"
            :label="$t('common.rules')"
            :item-text="item => item.name"
            :item-value="item => item.id"
            :items="rules"
            :error="!group.rules.length > 0 && showWarnings"
            class="pt-2"
            multiple
            chips
            deletable-chips
            clearable
            small-chips
            outlined
            hide-details
            @change="group.rules = $event.map(rule => rule.id)"
          />
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <!-- КНОПКА ОТМЕНА -->
          <v-btn
            text
            color="grey"
            @click="close"
          >
            {{ $t('common.close') }}
          </v-btn>

          <!-- КНОПКА СОЗДАТЬ -->
          <v-btn
            v-if="rulesGroupDialogMode === 'create'"
            color="blue darken-1"
            text
            :loading="loading.createRulesGroup"
            @click="createOrSave"
          >
            {{ $t('common.create') }}
          </v-btn>

          <!-- КНОПКА СОХРАНИТЬ -->
          <v-btn
            v-if="rulesGroupDialogMode === 'edit'"
            color="blue darken-1"
            text
            :loading="loading.editRulesGroup"
            @click="createOrSave"
          >
            {{ $t('common.save') }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapFields }     from 'vuex-map-fields';
import {mapGetters}      from 'vuex';

export default {
  name: 'GroupsRulesGroupDialog',

  data() {
    return {
      localRules: [],
      initial: {},
      groupName: ''
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'groups/dialogs',
      rulesGroupDialogMode: 'groups/rulesGroupDialogMode',
      rules: 'groups/rules',
      showWarnings: 'groups/showWarnings',
      loading: 'groups/loading'
    }),

    ...mapFields('groups', ['group']),
  },

  created() {
    this.localRules = this.rules.filter(rule => this.group.rules.indexOf(rule.id) > -1);

    this.initial = this.$_.cloneDeep(this.group);
  },

  destroyed() {
    this.group.name = null;
    this.group.rules = [];
    if (this.group.name) {
      this.groupName = this.group.name;
    }
  },

  methods: {
    close() {
      if (!this.$_.isEqual(this.group, this.initial)) {
        this.confirmation = confirm(this.$t('common.closeDialogConfirmation'));
        if (!this.confirmation) return;
      }

      this.$store.dispatch('groups/closeDialog', 'rulesGroupDialog');
      this.$store.dispatch('groups/setWarning', false);
    },

    changeGroupName(event) {
      if (event === '') {
        this.group.name = null;
      } else {
        this.group.name = event;
      }
    },

    async createOrSave() {
      if (this.group.name && this.group.rules && this.group.rules.length > 0) {
        if (this.rulesGroupDialogMode === 'create') {
          await this.$store.dispatch('groups/createRulesGroup', {
            rule_ids: this.group.rules,
            name: this.group.name
          });
          this.$store.dispatch('groups/clearGroup');
          this.$store.dispatch('groups/closeDialog', 'rulesGroupDialog');
        } else {
          await this.$store.dispatch('groups/editRulesGroup', {...this.group});
          this.$store.dispatch('groups/clearGroup');
          this.$store.dispatch('groups/closeDialog', 'rulesGroupDialog');
        }
        return this.$store.dispatch('groups/setWarning', false);
      } else {
        return this.$store.dispatch('groups/setWarning', true);
      }
    }
  }
};
</script>