<template>
  <v-dialog
    :value="dialogs.share"
    max-width="500px"
    scrollable
    @click:outside="close"
    @keydown.esc="close"
  >
    <v-card :loading="loading.share">
      <!-- ЗАГОЛОВОК -->
      <v-card-title class="pb-0">
        <span class="headline">
          {{ $t('common.share') }}
        </span>
      </v-card-title>

      <!-- ОКНО -->
      <v-card-text style="max-height: 700px;">
        <v-row>
          <v-col :cols="12">
            <div>
              <!-- ВЫБРАТЬ ВСЕ -->
              <v-btn
                color="primary"
                x-small
                style="margin-left: 0px;"
                class="my-1"
                outlined
                @click="selectAll"
              >
                {{ $t('common.selectAll') }}
              </v-btn>

              <!-- УБРАТЬ ВСЕ -->
              <v-btn
                color="primary"
                x-small
                style="margin-left: 0px;"
                class="my-1 ml-1"
                outlined
                @click="unselectAll"
              >
                {{ $t('common.unselectAll') }}
              </v-btn>
            </div>

            <!-- ДЕРЕВО ПОЛЬЗОВАТЕЛЕЙ -->
            <v-treeview
              v-model="permissions"
              selectable
              dense
              open-on-click
              selected-color="primary"
              :items="items"
            >
              <template v-slot:prepend="{ item }">
                <!-- ИКОНКА - ПОЛЬЗОВАТЕЛЬ -->
                <v-icon
                  v-if="item.id.search('user') !== -1"
                  size="16"
                  class="d-block text-center"
                >
                  fas fa-user
                </v-icon>

                <!-- ИКОНКА - ПРОСМОТР -->
                <v-icon
                  v-if="item.id.search('view') !== -1"
                  size="16"
                  class="d-block text-center"
                >
                  fas fa-eye
                </v-icon>

                <!-- ИКОНКА - РЕДАКТИРОВАТЬ -->
                <v-icon
                  v-if="item.id.search('edit') !== -1"
                  size="16"
                  class="d-block text-center"
                >
                  fas fa-edit
                </v-icon>

                <!-- ИКОНКА - РАЗДАВАТЬ -->
                <v-icon
                  v-if="item.id.search('share') !== -1"
                  size="16"
                  class="d-block text-center"
                >
                  fas fa-share-alt
                </v-icon>
              </template>
            </v-treeview>

            <!-- ВЫБОР РЕЖИМА -->
            <div>
              <v-radio-group
                v-model="mode"
                row
                dense
                style="margin-top: 0px;"
                hide-details
                class="mt-3"
              >
                <v-radio
                  :label="$t('dialogs.accounts.multipeShare.addPermissions')"
                  :value="1"
                />
                <v-radio
                  :label="$t('dialogs.accounts.multipeShare.removePermissions')"
                  :value="-1"
                />
              </v-radio-group>
            </div>
          </v-col>
        </v-row>
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <v-btn
          color="blue darken-1"
          text
          @click="close"
        >
          {{ $t('common.close') }}
        </v-btn>
        <v-btn
          color="blue darken-1"
          text
          :loading="loading.share"
          :disabled="$_.isEmpty(permissions)"
          @click="savePermissions"
        >
          {{ $t('common.save') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'GroupsDialogShare',

  data() {
    return {
      permissions: [],
      mode: 1,
      allItems: [],
      users: [],
    };
  },

  computed: {
    ...mapGetters({
      profile: 'main/profile',
      groups: 'groups/groups',
      dialogs: 'groups/dialogs',
      loading: 'groups/loading',
    }),

    items() {
      let items = [];

      this.users.forEach(user =>  {
        if (user.id === this.profile.id) return;
        const item = {
          id: 'user-' + user.id,
          name: user.display_name !== null ? user.display_name : user.login
        };
        item.children = [
          { id: `${user.id}-view`, name: this.$t('dialogs.accounts.share.permissions.view') },
          { id: `${user.id}-edit`, name: this.$t('common.edit') },
          { id: `${user.id}-share`, name: this.$t('dialogs.accounts.share.permissions.share') },
        ];

        this.allItems = this.allItems.concat(item.children.map(child => child.id));

        items.push(item);
      });

      return items;
    }
  },

  created() {
    this.loadUsers();
  },
        
  methods: {
    close() {
      if (this.permissions.length !== 0 || this.mode === -1) {
        this.confirmation = confirm(this.$t('common.closeDialogConfirmation'));
        if (!this.confirmation) return;
      }

      this.$store.dispatch('groups/closeDialog', 'share');
    },

    async loadUsers() {
      try {
        const response = await this.api('/users/all');
        if (response && response.data && response.data.data) {
          this.users = response.data.data.filter(user => parseInt(user.status) === 1);
        }
      } catch(error) {
        console.error(error);

        this.$store.dispatch('main/alert', {
          color: 'error',
          message: this.$t('common.somethingWentWrong')
        });
      }
    },

    async savePermissions() {
      await this.$store.dispatch('groups/setLoading', {
        param: 'share',
        value: true
      });

      try {
        const data = {
          ids: this.groups.selected.map(group => group.id),
          permissions: this.permissions,
          mode: this.mode,
        };

        const response = await this.api.post('/fb_rules/update_groups_permissions', data);
        if (response && response.data && response.data.success) {
          this.$store.dispatch('groups/closeDialog', 'share');

          this.$store.dispatch('main/alert', {
            color: 'success',
            message: this.$t('common.success')
          });
        }
        else {
          throw response;
        }
      } catch(error) {
        console.error(error);

        this.$store.dispatch('main/alert', {
          color: 'error',
          message: this.$t('common.somethingWentWrong')
        });
      }

      this.$store.dispatch('groups/setLoading', {
        param: 'share',
        value: false
      });
    },

    selectAll() {
      this.permissions = [];
      this.permissions = this.permissions.concat(this.allItems);
    },

    unselectAll() {
      this.permissions = [];
    }
  }
};
</script>